.recent-posts {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 370px;
  border-radius: 3px;
  background-color: $white;
  text-decoration: none;
  outline: 0;
  &__img {
    width: 100%;
    height: 230px;
    overflow: hidden;
    border-radius: 3px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; } }
  &__alert {
    display: flex;
    justify-content: center;
    margin-top: -12px; }
  &__content {
    padding: 0 5rem 4rem;
    text-align: center; }
  &__title {
    padding: 2.8rem 0 1rem; }
  &__divider {
    margin: 1.8rem auto 1rem; } }

.new-posts {
  display: flex;
  width: 770px;
  border-radius: 3px;
  background-color: $white2;
  text-decoration: none;
  outline: 0;
  &__img {
    width: 270px;
    height: 260px;
    background-position: center;
    background-size: cover; }
  &__alert {
    display: flex;
    justify-content: center;
    margin-top: -12px; }
  &__content {
    padding: 5.2rem 2.8rem 0; }
  &__divider {
    margin: 1.5rem 0; }
  &__txt {
    width: 408px; }
  &__data {
    padding-top: 2.7rem; } }

.comment {
  display: flex;
  background-color: #f6f6f6;
  width: 353px;
  &__img {
    width: 85px;
    height: 85px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; } }
  &__content {
    padding: 0 0 1.2rem 2.1rem;
    text-decoration: none;
    &:hover .comment__txt {
      color: $greenLight; } }
  &__txt {
    width: 215px;
    padding-bottom: 1.2rem; } }


.author {
  background-color: $white;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 210px;
  &__img {
    width: 155px;
    height: 155px;
    border-radius: 50%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; } }
  &__content {
    padding: 3rem 0.3rem;
    text-align: center; }
  &__position {
    font-size: 1.1rem;
    text-transform: uppercase;
    letter-spacing: 2.2px;
    color: $greenLight;
    padding: 1rem 0; } }
