.btn {
  outline: 0;
  cursor: pointer;
  text-decoration: none;
  border: 1px solid; }

.btn-circle {
  border-radius: 50px; }

.btn-slider {
  width: 7rem;
  height: 7rem;
  border-color: $gray;
  background-color: $grayInfo;
  &:hover {
    background-color: $greenLight; } }

.btn-double {
  display: flex;
  justify-content: space-between;
  border-radius: 2px;
  text-transform: uppercase;
  text-align: center;
  color: $white;
  &__txt {
    width: 156px;
    text-shadow: 0 1px 0 $gray4;
    text-align: center;
    font-size: 1.3rem;
    font-weight: 700;
    line-height: .74;
    letter-spacing: 1.3px;
    padding: 2.1rem 0; }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5.2rem;
    background-color: $greenDark; }
  &__icon-dark {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5.2rem;
    background-color: $transponder;
    border-left: 1px solid $white; } }

.btn-green {
  width: 20.8rem;
  background-color: $greenLight;
  border: none;
  &:hover {
    background-color: $greenDark; } }

.btn-dark {
  background-color: $transponder;
  &:hover {
    background-color: $greenDark;
    border-color: $greenDark; } }

.btn-small {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  width: 4.7rem;
  height: 4.7rem; }

.btn_green {
  background-color: $greenLight;
  &:hover {
    background-color: $greenDark; } }

.btn-social {
  outline: 0;
  text-decoration: none; }
