
@import "config";
@import "media";
@import "mixins";
@import "reset";
@import "vars";
@import "blocks/all_blocks";

* {
  box-sizing: border-box; }

html,
body {
  font-family: $Roboto;
  font-size: 10px;
  font-weight: normal;
  background-color: $grayInfo; }

.step {
  margin: 3rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 35rem; }

.titleBody {
  color: $gray7;
  font-weight: bold;
  font-size: 3.6rem;
  text-align: center;
  margin: 3rem 0; }

.position {
  display: flex;
  justify-content: space-between;
  width: 17rem; }

.position2 {
  display: flex;
  justify-content: space-between;
  width: 45rem; }
